@media screen and (min-width: 320px) and (max-width: 767px){
    .list-cards{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    .event{
        width: 138px;
        height: 242px;
        list-style: none;
        padding: 10px 9px 0 0;
        margin-bottom: 33px;
    }
    .event:nth-child(odd){
        margin-left: 10px;
    }
    .event-19{
        margin-bottom: 0;
    }
    .event-20{
        margin-bottom: 0;
    }
    .event img{
        width: 128px;
        height: 161px;
        border-radius: 50px 0px;
    }
    .event h3{
        height: 31px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        color: #DC56C5;
        margin-top: 5px;
    }
    .event h4{
        height: 14px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        color: #FFFFFF;
        margin: 5px 0;
    }
    .event p {
        height: 12px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 600;
        font-size: 12px;
        line-height: 15px;
        color: #FFFFFF;
        text-align: center;
    }
    .nav-bar{
        margin-top: 55px;
    }
    .ul-nav-bar{
        display: flex;
        justify-content: center;
    }
    .p-nav{
        width: 6px;
        height: 17px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        color: #FFFFFF;
    }
    .p-nav6{
        width: 9px;
    }
    .p-nav7{
        width: 17px;
    }
    .li-nav{
        width: 30px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 2px;
    }
    .li-nav:last-child{
        margin-right: 0;
    }
    .li-nav:hover{
        border: #DC56C5 1px solid;
        border-radius: 20px 0;
    }
}
